.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.contacts__item {
  padding-left: 44px;
  padding-right: 39px;
  padding-bottom: 28px;

  &:last-child {
    padding-bottom: 0;
  }
}

.contacts__head {
  margin: 0;
  width: 250px;
  padding-top: 50px;
  padding-bottom: 44px;
  font-size: 24px;
  font-weight: 200;
  line-height: 30px;
  color: $text-blue;
}

.contacts__subtitle {
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
}

.contacts__contact {
  text-decoration: none;
  color: $pink;
  font-style: normal;
  font-size: 24px;
  font-weight: 200;
  line-height: 30px;
}

@media (min-width: $tablet-width) {
  .contacts__wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .contacts__item--call {
    flex-grow: 1;
  }

  .contacts__item--write {
    flex-grow: 1;
  }

  .contacts__item--come {
    flex-grow: 0;
  }
}

@media (min-width: $desktop-width) {
  .contacts__item {
    padding-right: 100px;
    padding-left: 0;
  }

  .contacts__item--write {
    flex-grow: 1;
    order: 2;
    padding-right: 0;
  }

  .contacts__contact--address {
    width: 211px;
  }
}
